/* Side Navbar */
.navbar {
  background-color: var(--section-bgd);
  color: var(--white);
  position: fixed;
  top: 0;
  left: -100%;
  bottom: 0;
  opacity: 0;
  overflow-y: scroll;
  transition: all 0.5s ease-in-out;
  z-index: 99999;
}

.navbar.active {
  left: 0;
  opacity: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar::-webkit-scrollbar {
  scroll-margin: 0;
}

.navbar-container {
  padding: 1rem 1.5rem;
}

.top-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
}

.img__cover {
  height: 100px;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  object-fit: cover;
  object-position: bottom;
}
.profile-pic-small {
  height: 100%;
  width: 100%;
  border-radius: 50%;
  margin-bottom: 1rem;
  border: 5px solid var(--nav-deep-blue);
}

.profile-name {
  font-size: 1rem;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  text-decoration: none;
  color: var(--white);
  margin-bottom: 1rem;
  cursor: pointer;
}

.nav-links {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.nav-links li,
.mid-details li {
  list-style: none;
}

.nav-link {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  text-decoration: none;
  color: var(--white);
  margin: 0 0.3rem;
  padding: 0.7rem;
  background-color: var(--nav-deep-blue);
  border-radius: 50%;
  transition: all 0.3s ease-in-out;
}

.nav-link:hover {
  background-color: var(--nav-blue);
}

.mid-details {
  padding-left: 3.6rem;
  /* background-color: red; */
}

.flex {
  display: flex;
}

.mid-links {
  display: flex;
  align-items: center;
  color: var(--nav-grey);
  cursor: pointer;
  text-decoration: none;
  padding: 1rem 0;
  font-size: 0.9rem;
  transition: all 0.3s ease-in-out;
}

.mid-links.active {
  /* color: var(--nav-blue); */
}

.mid-links:hover {
  color: var(--nav-blue);
}

.mid-links:hover .mid-icon {
  color: var(--nav-blue);
}

.mid-icon {
  margin-right: 1rem;
  font-size: 1.5rem;
}

.copy {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.copyright {
  color: var(--white);
  font-size: 65%;
  font-family: "Quicksand", sans-serif;
}
